.products {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;
    padding: 0;
    margin: 0;
    list-style-type: none;
}

.products li {
    flex: 1 1 29rem;
    height: 47rem;
    padding: 1rem;
    margin: 1rem;
}

.product {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
}

.product img {
    max-width: 37rem;
    max-height: 37rem;
}

.productPrice {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.productPrice div, 
.productPrice button {
    text-align: center;
}

.productPrice div {
    font-size: 2rem;
}

.button {
    padding: 1rem;
    border: 0.1rem #c0c0c0 solid;
    background-color: #f0f0f0;
    cursor: pointer;
}

.button:hover {
    border: 0.1rem #808080 solid
}

.button.primary {
background-color:yellow;
}



